<template>
  <div class="container">
    <h1>未来の混雑予測</h1>
    <h2 id="current-time" class="current-time">
      現在時刻：{{ currentTime }}
    </h2>

    <div class="box">
      <h2>4時間後までの混雑予測</h2>
      <div class="heatmap-wrapper">
        <div class="heatmap-legend-vertical">
          <span>多い</span>
          <div class="gradient-bar-vertical"></div>
          <span>少ない</span>
        </div>
        <div class="heatmap-main">
          <div class="heatmap-points">
            <div
              v-for="(point, i) in predictions"
              :key="i"
              class="heat-point"
              :title="`${point.count}人`"
              :style="getPointStyle(point.count)"
            ></div>
          </div>
          <div class="time-labels">
            <div
              v-for="(point, i) in predictions"
              :key="'label-' + i"
              :style="getLabelStyle(point.time)"
            >
              {{ formatTime(point.time) }}
            </div>
            <div :style="getLabelStyle(extraTime)">
              {{ formatTime(extraTime) }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      predictions: [],
      currentTime: '',
      maxCount: 1,
      extraTime: '',
      apiUrl: 'https://funma-app-backend.onrender.com'
    };
  },
  mounted() {
    this.updateCurrentTime();
    setInterval(this.updateCurrentTime, 1000);
    this.fetchPredictions();
    setInterval(this.fetchPredictions, 3600000); // 1時間ごと
  },
  methods: {
    updateCurrentTime() {
      const nowUTC = new Date();
      const jst = new Date(nowUTC.toLocaleString('en-US', { timeZone: 'Asia/Tokyo' }));
      const pad = (n) => String(n).padStart(2, '0');
      this.currentTime = `${jst.getFullYear()}/${pad(jst.getMonth() + 1)}/${pad(jst.getDate())} ` +
        `${pad(jst.getHours())}:${pad(jst.getMinutes())}:${pad(jst.getSeconds())}`;
    },
    fetchPredictions() {
      fetch(`${this.apiUrl}/predict`)
        .then(res => res.json())
        .then(json => {
          this.predictions = json.predictions;
          this.maxCount = Math.max(...this.predictions.map(p => Number(p.count) || 0));

          const last = new Date(this.predictions[this.predictions.length - 1].time.replace(' ', 'T'));
          last.setMinutes(last.getMinutes() + 15);
          this.extraTime = last.toISOString().slice(0, 16).replace('T', ' ');
        })
        .catch(err => console.error('Fetch error:', err));
    },
    getPointStyle(count) {
      const value = Number(count) || 0;
      const ratio = this.maxCount > 0 ? value / this.maxCount : 0;
      let r = 0, g = 0, b = 0;
      if (ratio < 0.33) {
        g = Math.floor(255 * (ratio / 0.33));
        b = 255;
      } else if (ratio < 0.66) {
        r = Math.floor(255 * ((ratio - 0.33) / 0.33));
        g = 255;
        b = 255 - r;
      } else {
        r = 255;
        g = Math.floor(255 * (1 - (ratio - 0.66) / 0.34));
      }
      const height = 10 + 100 * ratio;
      return {
        backgroundColor: `rgb(${r}, ${g}, ${b})`,
        height: `${height}px`,
        width: '3.8%'
      };
    },
    formatTime(time) {
      return time.slice(11);
    },
    getLabelStyle(time) {
      const now = new Date();
      const labelTime = new Date(time.replace(' ', 'T'));
      const minute = time.slice(14, 16);
      return {
        flex: '1',
        textAlign: 'center',
        fontSize: minute === '00' ? '1rem' : '0.8rem',
        fontWeight: minute === '00' ? 'bold' : 'normal',
        opacity: labelTime < now ? 0.4 : 1
      };
    }
  }
};
</script>

